<template>
  <div class="camera-test-preview">
    <img class="img-fluid" :src="this.fileSrc">
    <div class="dialog-footer">
      <el-button type="danger" @click="handleCancel">Cancel</el-button>
      <el-button type="primary" @click="handleConfirm">Confirm</el-button>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      fileSrc: ''
    }
  },
  mounted () {
    this.getFile()
  },
  methods: {
    getFile () {
      let file = this.$store.getters['getPreviewFile']
      this.fileSrc = URL.createObjectURL(file)
    },
    handleCancel () {
      this.$store.dispatch('destroyPreviewFile')
      this.$router.go(-1)
    },
    handleConfirm () {}
  },
  beforeDestroy () {
    this.$store.dispatch('destroyPreviewFile')
  }
}
</script>

<style>
.camera-test-preview {
  margin: 0 auto;
  text-align: center;
  padding: 10px;
}

.camera-test-preview img {
  max-width: 100%;
  max-height: 500px;
  margin: 10px;
}
</style>
